import React, { Component } from 'react'
import Spinner from 'react-spinkit'
import AthleteFilters from './AthleteFilters'
import AthleteCard from './AthleteCard'
import AthleteDetailCard from './AthleteDetailCard'
import './AthleteList.scss'

class AthleteList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showFilters: false,
      autoPlay: false,
      autoRefresh: false,
      daysAgo: 90,
      refreshInterval: 10000,
    }

    this.refreshTimer = null
    this.autoPlayTimer = null

    this.onClickToggleFilters = this.onClickToggleFilters.bind(this)
    this.onClickRefresh = this.onClickRefresh.bind(this)
    this.onClickAutoPlay = this.onClickAutoPlay.bind(this)
    this.triggerAutoScripts = this.triggerAutoScripts.bind(this)
  }

  onClickToggleFilters() {
    return this.setState({ showFilters: !this.state.showFilters })
  }

  onClickAthleteCard(result) {
    const { onSelectResult } = this.props
    return onSelectResult(result)
  }

  onCloseDetail() {
    const { onUnselectResult } = this.props
    return onUnselectResult()
  }

  onClickRefresh() {
    const { fetchGraceNoteData } = this.props
    const newDaysAgo = this.state.daysAgo
    this.setState({ daysAgo: newDaysAgo }, () => fetchGraceNoteData({ daysAgo: newDaysAgo }))
  }

  onClickAutoPlay() {
    const _this = this
    this.setState({ autoPlay: !this.state.autoPlay, autoRefresh: !this.state.autoRefresh }, () => _this.triggerAutoScripts())
  }


  triggerAutoScripts() {
    const _this = this
    if (this.state.autoRefresh === true) {
      this.refreshTimer = window.setInterval(() => {
        _this.onClickRefresh()
      }, this.state.refreshInterval)
    } else {
      window.clearInterval(this.refreshTimer)
    }

    // if (this.state.autoRefresh === true) {
    //   this.autoPlayTimer = window.setInterval(() => {
    //     _this.onClickRefresh()
    //   }, this.state.refreshInterval)
    // } else {
    //   window.clearInterval(this.autoPlayTimer)
    // }

  }

  render() {
    const _this = this
    const { selectedResult, globeStatus, filterOptions, setFilters, phaseResults } = this.props
    const filteredResults = phaseResults.filtered
    const poiResults = phaseResults.poi
    const cardSource = poiResults.length > 0 ? poiResults : filteredResults
    const athleteCards = cardSource.map( r => <AthleteCard key={r.n_PhaseResultID} result={r} onClick={() => _this.onClickAthleteCard(r)} /> )

    const toggleCssClass = `float-right btn btn-link ${this.state.showFilters ? '' : 'text-white'}`
    const heading = cardSource.length > 0 ? `Found ${cardSource.length} medalists` : 'No medalist found'

    const loader = phaseResults.loading === true ? (
      <div className="d-flex p-3 align-items-center justify-content-center">
        <Spinner name="three-bounce" color="#00bc8c" />
      </div>
      ) : null

    const cssClass = globeStatus.viewDetails ? 'athlete--list--container athlete--list--container__detailed' : 'athlete--list--container'
    return (
      <div className={cssClass} id="athlete-list">
        <div className="athlete--list p-3">
          <div className="clearfix">
            <button onClick={this.onClickToggleFilters} className={toggleCssClass} title="Toggle filters">
              <i className="material-icons md-48">filter_list</i>
            </button>
            <button onClick={this.onClickRefresh} className="float-right btn btn-link text-white" title="Refresh data">
              <i className="material-icons md-48">refresh</i>
            </button>
            <button onClick={this.onClickAutoPlay} className="float-right btn btn-link text-white" title="Autoplay">
              <i className="material-icons md-48">play_arrow</i>
            </button>
            <h1>Aussie medalists</h1>
            {loader}
          </div>
          <AthleteFilters filterOptions={filterOptions} setFilters={setFilters} phaseResults={phaseResults} showFilters={this.state.showFilters} />

          <h4 className="my-3">{heading}</h4>
          {athleteCards}
        </div>
        <AthleteDetailCard selectedResult={selectedResult} onClick={() => _this.onCloseDetail()} />
      </div>
    );
  }
}

export default AthleteList