import fetch from 'isomorphic-fetch'
import * as types from '../constants/ActionTypes'
import countries from '../assets/data/countries.json'
import cities from 'cities.json'

export const requestGraceNoteData = () => ({
  type: types.REQUEST_GRACENOTE_DATA,
})

export const receiveGraceNoteData = (json) => ({
  type: types.RECEIVE_GRACENOTE_DATA,
  json
})

export const fetchGraceNoteData = (options) => {
  const { daysAgo = 60, nationality = 'Australia', hasMedal = true } = options
  const requestUrl = 'https://another-quang.github.io/build/assets/phaseresults.json'
  // const requestUrl = `http://gracenotelocal/api/phaseresult?daysAgo=${daysAgo}&nationality=${nationality}&hasMedal=${hasMedal}`
  return dispatch => {
    dispatch(requestGraceNoteData())
    return fetch(requestUrl)
      .then(response => response.json())
      .then(json => {
        const results = [];
        if (json.length > 0) {
          const subset = json.slice(0, 2)
          subset.forEach(r => {
            if (r.c_PhaseCountry !== null) {
              const countryGeo = countries.find(c => c.Country === r.c_PhaseCountry)
              const countryCode = countryGeo !== undefined ? countryGeo['Alpha-2 code'] : false
              const cityGeo = cities.find(c => c.name === r.c_PhaseCity && (c.country === countryCode))
              const geojson = cityGeo !== undefined ? cityGeo : (countryGeo !== undefined ? countryGeo : undefined)
              if (geojson !== undefined) {
                results.push({...r, geojson: geojson})
              }
            }
          })
        }
        dispatch(receiveGraceNoteData(results))
      })
  }
}

export const selectResult = selectedResult => ({
  type: types.SELECT_RESULT,
  selectedResult
})

export const unselectResult = selectedResult => ({
  type: types.UNSELECT_RESULT,
  selectedResult
})

export const setPOI = (lat, lng) => ({
  type: types.SET_POI,
  lat,
  lng
})

export const unsetPOI = () => ({
  type: types.UNSET_POI
})

export const setFilters = (newFilterValues) => ({
  type: types.SET_FILTER,
  newFilterValues
})