import * as THREE from 'three'

export default function CircleLineBufferGeometry(radius, segments, thetaStart, thetaLength, color = 0xff0000) {
  const args = {
    radius: radius || 50,
    segments: segments || 8,
    thetaStart: thetaStart || 0,
    thetaLength: thetaLength || (2 * Math.PI),
  };
  const geometry = new THREE.BufferGeometry();
  const delta = ((args.thetaStart + args.thetaLength) - args.thetaStart) / args.segments;
  const positions = []
  const colors = []
  const started = []
  // let now = new Date()
  // Adding random starting time
  // now = now.setSeconds(now.getSeconds() + THREE.Math.randFloat(-1,1))

  const now = THREE.Math.randFloat(-1, 1)
  for (let i = 0; i <= args.segments; i += 1) {
    const angle = args.thetaStart + (delta * i);
    const x = args.radius * Math.cos(angle);
    const y = args.radius * Math.sin(angle);
    positions.push(x, y, 0)
    const threeColor = new THREE.Color(color)
    colors.push(threeColor.r, threeColor.g, threeColor.b)
		started.push(now)
  }

  geometry.addAttribute( 'position', new THREE.Float32BufferAttribute( positions, 3 ) );
  geometry.addAttribute( 'color', new THREE.Float32BufferAttribute( colors, 3 ) );
  geometry.addAttribute( 'started', new THREE.Float32BufferAttribute( started, 1 ).setDynamic( true ));
  // geometry.addAttribute( 'size', new THREE.Float32BufferAttribute( sizes, 1 ).setDynamic( true ) );
  console.log(geometry)
  return geometry;
}
