import { REQUEST_GRACENOTE_DATA, RECEIVE_GRACENOTE_DATA, SET_POI, UNSET_POI, SET_FILTER } from '../constants/ActionTypes'

const initialState = {
  all: [], // Everything
  filtered: [], // Filtered
  poi: [], // Near POI
  loading: true,
  error: null
}

const sortBy = (key, order) => {
  const sortOrder = order === 'asc' ? 1 : -1
  return (a, b) => (a[key] > b[key]) ? sortOrder : ((b[key] > a[key]) ? -sortOrder : 0)
}

const phaseResults = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_GRACENOTE_DATA: {
      return { ...state, loading: true, error: null }
    }

    case RECEIVE_GRACENOTE_DATA: {
      if (action.json.length === 0) {
        return state
      }
      return { ...state, loading: false, all: action.json, filtered: action.json }
    }

    case SET_POI: {
      const distanceThreshold = 10
      const copyState = []

      state.filtered.forEach(x => {
        const a = x.geojson.lat - action.lat
        const b = x.geojson.lng - action.lng
        copyState.push({ ...x, distance: Math.sqrt(a * a + b * b) })
      })

      const results = copyState.filter(x => x.distance < distanceThreshold).sort(sortBy('distance', 'asc'))
      return { ...state, poi: results }
    }

    case UNSET_POI: {
      return { ...state, poi: [] }
    }

    case SET_FILTER: {
      const ignoreSportName = action.newFilterValues.sport === 'All' ? true : false
      const ignoreGender = action.newFilterValues.gender === 'All' ? true : false
      const ignoreMedal = action.newFilterValues.medal === 'All' ? true : false
      const ignoreName = action.newFilterValues.name === '' ? true : false
      let sortKey = ''
      let order = ''
      switch (action.newFilterValues.sortby) {
        case 'time-asc' :
          sortKey = 'd_PhaseDate'
          order = 'asc'
          break
        default:
          sortKey = 'd_PhaseDate'
          order = 'desc'
          break
      }

      return { ...state,
        poi: [],
        filtered: state.all.filter(x => {
          return (x.c_Sportname === action.newFilterValues.sport || ignoreSportName)
            && (x.c_EventGender === action.newFilterValues.gender || ignoreGender)
            && (x.n_RankMedal === parseInt(action.newFilterValues.medal, 10) || ignoreMedal)
            && ((x.c_Person !== null ? x.c_Person.toUpperCase().includes(action.newFilterValues.name.toUpperCase()) : false) || ignoreName)
        }).sort(sortBy(sortKey, order))
      }
    }
    default:
      return state
  }
};

export default phaseResults
