import React, { Component } from 'react'

class AthleteFilters extends Component {
  constructor(props) {
    super(props)

    this.resetValues = {
      sport: 'All',
      gender: 'All',
      medal: 'All',
      name: '',
      sortby: 'time-desc'
    }

    this.state = { ...this.resetValues }

    this.onChange = this.onChange.bind(this)
    this.onReset = this.onReset.bind(this)
  }

  onChange() {
    const { setFilters } = this.props
    const newFilterValues = {
      sport: document.getElementById('sport-name').value,
      gender: document.getElementById('sport-gender').value,
      medal: document.getElementById('sport-medal').value,
      name: document.getElementById('athlete-name').value,
      sortby: document.getElementById('sort-by').value,
    }
    return this.setState(newFilterValues, setFilters(newFilterValues))
  }

  onReset() {
    const { setFilters } = this.props
    return this.setState({ ...this.resetValues }, setFilters(this.resetValues))
  }

  render() {
    const { filterOptions, showFilters } = this.props
    const sportNames = filterOptions.sport.map(x => (<option key={`sport-${x}`} value={x}>{x}</option>))
    const genderNames = filterOptions.gender.map(x => (<option key={`gender-${x}`} value={x}>{x}</option>))
    const medalNames = filterOptions.medal.map(x => (<option key={`medal-${x}`} value={x}>{x}</option>))
    const cssClass = showFilters === true ? '' : 'd-none'
    return (
      <div className={cssClass}>
        <div className="form-row">
          <div className="col-6">
            <div className="form-group">
              <label htmlFor="sport-name">Sport</label>
              <select id="sport-name" name="sport" value={this.state.sport} className="form-control" onChange={this.onChange}>
                {sportNames}
              </select>
            </div>
          </div>
          <div className="col-3">
            <div className="form-group">
              <label htmlFor="sport-gender">Gender</label>
              <select id="sport-gender" name="gender" value={this.state.gender} className="form-control" onChange={this.onChange}>
                {genderNames}
              </select>
            </div>
          </div>
          <div className="col-3">
            <div className="form-group">
              <label htmlFor="sport-medal">Medal</label>
              <select id="sport-medal" name="medal" value={this.state.medal} className="form-control" onChange={this.onChange}>
                {medalNames}
              </select>
            </div>
          </div>
        </div>
        <div className="form-row">
          <div className="col-9">
            <div className="form-group">
              <label htmlFor="athlete-name">Athelete name</label>
              <input id="athlete-name" name="name" value={this.state.name} type="text" className="form-control" onChange={this.onChange} />
            </div>
          </div>
          <div className="col-3">
            <div className="form-group">
              <label htmlFor="sort-by">Sort by</label>
              <select id="sort-by" name="sortby" value={this.state.sortby} className="form-control" onChange={this.onChange}>
                <option value="time-desc">Newest first</option>
                <option value="time-asc">Oldest first</option>
              </select>
            </div>
          </div>
        </div>

        <button className="btn btn-link p-0" onClick={this.onReset}>Reset filters</button>
      </div>
     )
  }
}

export default AthleteFilters